/* Reset CSS */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global Styles */
body {
  font-family: 'Open Sans', sans-serif;
  background-color: #151515;
  color: #ffffff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.about-sections {
  padding-left: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.headings {
  text-align: left;
  padding-left: 20px;
  padding-bottom: 50px;
  font-size: 4rem;
  /* padding-top: 30px; */
}

.about-section {
  flex-basis: 48%; /* Adjust as needed */
}

/* Navbar Styles */
.navbar {
  font-family: JetBrains Mono, monospace;
  background-color: black;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center; /* Ensure vertical centering */
  padding: 20px 50px; /* Add some padding on the left and right */
  width: 100%;
  box-sizing: border-box; /* Include padding and border in the element's total width */
}

.navbar h1 {
  font-size: 1.8rem;
  margin: 0;
}

.nav-links {
  list-style-type: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.2rem;
  transition: color 0.3s;
}

.nav-links li a:hover {
  color: #64ffda;
}

/* Hero Section Styles */
.hero, .page-hero{
  font-family: JetBrains Mono, monospace;
  text-align: left;
  padding: 100px 50px;
  background: linear-gradient(45deg,#141414, #000000);
  background-size: 250% 250%;
  animation:  gradientAnimation 30s infinite;
  background-position: center;
  margin: 40px 20px;
  border-radius: 8px;
  height: 80vh;
}


.page-hero{
  padding: 50px 50px;

}


.btn {
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  border: 2px solid #64ffda;
  background: none;
  color: #64ffda;
  text-decoration: none;
  border-radius: 8px;
  transition: background-color 0.3s, color 0.3s;
}

.btn-primary {
  display: inline-block;
  margin: 10px;
  padding: 10px 20px;
  font-size: 1rem;
  border: 2px solid #121212;
  background-color: #64ffda;
  /* color: #64ffda; */
  text-decoration: none;
  border-radius: 8px;
  transition: background-color 0.3s, color 0.3s;
}

.btn-primary:hover {
  background-color: #121212;
  color: #64ffda;
}

.btn:hover {
  background-color: #64ffda;
  color: #000000;
}


.hero-btns {
  padding-top: 20px;
}
/* About Section Styles */
.about-content {
  background-color: #000000;
  padding: 40px 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: left;
  margin: 40px 20px;
}

.about-content h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.about-content p {
  font-size: 1rem;
  margin-bottom: 20px;
}

/* Projects Section Styles */
.projects {
  padding-left: 20px;
  padding-right: 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 20px;
}

.project-card {
  margin: auto;
  background-color: #121212;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.project-card p {
  font-size: 1rem;
  margin-bottom: 20px;
}

.project-card a {
  display: inline-block;
  padding: 10px 20px;
  background-color: #64ffda;
  color: #121212;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.project-card a:hover {
  background-color: #1f1f1f;
  color: #64ffda;
}

/* Contact Section Styles
.contacts {
  padding: 50px 0;
} */

.contact-info {
  padding-left: 20px;
}

.contact-item {
  display: flex;
}

.contact-item i {
  margin-right: 10px;
}

/* Footer Styles */
.footer {
  text-align: center;
  padding: 20px 0;
  background-color: #151515;
  color: #ffffff;
}

.logo h1 a {
  text-decoration: none;
  transition: color 0.3s;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.8rem;
  color: #ffffff;
}

.logo h1 a:hover {
