}

.home {
  position: relative;
}

.social-icons {
  position: absolute;
  bottom: 20px;
  right: 20px;
  padding-right: 20px;
}

.download-resume{
  position: absolute;
  top: 20px;
  right: 20px;
  padding-right: 20px;

}

.social-icon {
  font-size: 2em; /* Adjust the size as needed */
  color: white; /* Set the color to white */
  margin-left: 10px; /* Add some space between icons */
}

.hero h1, .hero h2, .hero p {
  font-family: 'Jetbrains Mono', monospace;
}

.hero-subtitle, .hero-heading1, .hero-heading2 {
  overflow: hidden;
  white-space: nowrap;
  animation: typing 3s steps(100, end), color-change forwards;
  position: relative; /* Add relative positioning */
  color: transparent; /* Hide the text */
}

.hero-heading1 {
  animation: typing1 0.3s steps(4, end), color-change forwards;
  font-size: 5rem;
  margin-bottom: 10px;
}

.hero-heading2 {
  font-size: 5rem;
  animation: typing2 1.7s steps(24, end), color-change forwards;
  animation-delay: 0.3s;
  margin-bottom: 50px;
}

.hero-subtitle {
  font-size: 1.2rem;
  animation: typing3 3s steps(50, end), color-change forwards;
  animation-delay: 2s;
  margin-bottom: 50px;
}

#animated-heading {
  display: inline-block;
  text-align: left;
  padding-left: 20px;
  padding-bottom: 50px;
  font-size: 4rem;
}

#animated-heading span {
  display: inline-block;
  opacity: 0; /* Start invisible */
  transform: rotateX(0deg);
  transition: opacity 0.2s ease-in;
}

#animated-heading span.visible {
  opacity: 1; /* Become visible during the animation */
}

.head {
  display: inline-block;
  font-size: 1.2rem;
  font-family: 'JetBrains Mono', monospace;
  color: #64ffda;
  margin-bottom: 10px;
  margin-top: 20px;
}
/* src/components/SkillBar.css */
.skill {
  margin: 20px 0;
  text-align: left;
}

.skill-name {
  font-size: 1em;
  margin-bottom: 8px;
  color: #64ffda;
}

.skill-bar {
  background: #333;
  border-radius: 10px;
  overflow: hidden;
  height: 10px;
  position: relative;
}

.skill-level {
  height: 100%;
  background: linear-gradient(90deg, #64ffda, #0a192f);
  width: 0;
  transition: width 1s ease-in-out;
  border-radius: 10px 0 0 10px;
  position: relative;
}

.skill-level::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 10px;
  background: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.skill-level.filled::after {
  opacity: 1;
  animation: shine 1s linear infinite;
}

@keyframes shine {
  0% {
      right: 100%;
      opacity: 0;
  }
  50% {
      right: 50%;
      opacity: 1;
  }
  100% {
      right: 0;
      opacity: 0;
  }
}

@keyframes typing1 {
  0% { width: 0; }
  100% { width: 10%} /* Remove the cursor at the end of the animation */
}

@keyframes typing2 {
  0% { width: 0; }
  100% { width: 60%} /* Remove the cursor at the end of the animation */
}

@keyframes typing3 {
  0% { width: 0; }
  100% { width: 90%} /* Remove the cursor at the end of the animation */
}

@keyframes color-change {
  0% { color: transparent; }
  100% { color: white; } /* Change the color to the initial color */
}

@keyframes gradientAnimation {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.hero-subtitle.typing-done::after, .hero-heading1.typing-done::after, .hero-heading2.typing-done::after {
  animation: none; /* Stop the blinking after typing */
}

